/**
 * SaveData Widget
 *
 * I'm a Widget.
 * I can be placed into a Workspace that allows children (canHaveChildren = true)
 * I can also access the context for the workspace as seen in the example below
 * - Context is a great place to pass in libraries, widgetApis, data, etc that need to be accessible in the Widget.
 * - my props are very interesting! If you configure userConfig, the keys will be the prop names and you can access them.
 *
 * @pacakge SaveData
 */
import { useContext, useEffect, useState } from "react";
import {
    Widget,
    Paragraph3,
    ButtonIcon3,
    Paragraph,
    ThemeContext,
    DashboardContext,
    DashPanel3,
} from "@dash";
import { SaveDataContext } from "../contexts";

export const SaveDataWidget = (props) => {
    const { listeners, id, uuid } = props;
    const { widgetApi } = useContext(DashboardContext);

    // grab the filename!
    const { filename } = useContext(SaveDataContext);

    // handler map to pas to the registration of listeners
    const handlers = {
        handleSearchChange2: handleSearchChange2,
    };
    // register our listeners (from our config, and injected into this widget)
    useEffect(() => {
        if (listeners !== null && widgetApi !== null) {
            try {
                // register listeners
                widgetApi.registerListeners(listeners, handlers, uuid);
            } catch (e) {
                console.log("Save data widget ", e.message);
            }
        }
    }, [listeners, widgetApi, uuid]);

    const [dataToSave, setDataToSave] = useState("");

    function handleSearchChange2(data) {
        setDataToSave(() => data.query);
    }

    function handleStoreData() {
        try {
            widgetApi.storeData({
                data: dataToSave,
                filename: filename,
                callbackComplete: handleSaveDataComplete,
                callbackError: handleSaveDataError,
                append: true,
                uuid,
            });
        } catch (e) {
            console.log("error storing data ", e.message);
        }
    }

    function handleSaveDataComplete(e, data) {
        try {
            widgetApi.publishEvent(
                `SaveDataWidget[${id}].saveDataComplete`,
                data
            );
        } catch (e) {
            console.log("save data error", e.message);
        }
    }

    function handleSaveDataError(e, error) {
        console.log("save data error ", e, JSON.stringify(error));
    }

    {
        /* Note: We are using the Dashboard components (themed for your pleasure) to build this Widget render, you can use whatever you want! */
    }

    console.log("save data props", props);
    return (
        <Widget {...props}>
            <DashPanel3>
                <DashPanel3.Header title="Save" />
                <DashPanel3.Body>
                    {filename !== null && (
                        <div className={`rounded-b p-2 h-full`}>
                            <Paragraph
                                text={dataToSave}
                                textColor={"text-gray-400"}
                            />
                        </div>
                    )}
                    {filename === null && (
                        <div className={`rounded p-2 h-full bg-red-700`}>
                            <Paragraph3
                                text={
                                    "Please edit the layout, and add a filename to the SaveDataWorkspace"
                                }
                            />
                        </div>
                    )}
                </DashPanel3.Body>
                <DashPanel3.Footer>
                    <div className="flex flex-row justify-end w-full">
                        <ButtonIcon3
                            icon="save"
                            text={"Save"}
                            onClick={handleStoreData}
                        />
                    </div>
                </DashPanel3.Footer>
            </DashPanel3>
        </Widget>
    );
};
