import {
    Panel2,
    Panel3,
    Paragraph,
    SubHeading3,
    Tag,
    Tag2,
    Tag3,
    ThemeContext,
} from "@dash";
import { DragDropWidget } from "./DragDropWidget";

export const CustomHitRL = ({
    id,
    width,
    type = "hit-widget",
    hit,
    onDropItem = null,
}) => {
    function renderHit(hit) {
        return (
            hit !== undefined && (
                <Panel2
                    className={
                        "rounded h-fit shadow rounded-lg min-h-64 min-h-64"
                    }
                    padding={false}
                >
                    <div className="flex flex-col h-full rounded-lg overflow-clip border border-gray-600 shadow">
                        <div className="flex bg-gray-400 border-b border-indigo-300 rounded-t h-fit min-h-64 justify-center items-center p-6 bg-white">
                            <img
                                src={hit.image_link}
                                className="p-0 object-contain"
                            />
                        </div>
                        <div className="flex flex-col p-4 justify-between">
                            <div className="flex flex-col space-y-4 p-4">
                                <Paragraph text={hit.title} padding={false} />
                                <div className="text-gray-500 text-sm">
                                    {hit.description.substring(0, 100)}
                                </div>
                            </div>
                            <div className="flex flex-col p-4 space-y-1">
                                <div className="flex flex-row space-x-1">
                                    <Tag2
                                        text={`$${hit.price}`}
                                        backgroundColor={"bg-green-700"}
                                    />
                                    <Tag2
                                        text={`${hit.brand}`}
                                        backgroundColor={"bg-orange-800"}
                                    />
                                </div>
                                <div className="flex flex-row space-x-1">
                                    <Tag2 text={`${hit.gender}`} />
                                    <Tag2 text={`${hit.condition}`} />
                                </div>
                                {/* <div className="flex flex-col">
                  <Tag text={`${hit.objectID}`} textSize="text-xs" />
                </div> */}
                            </div>
                        </div>
                    </div>
                </Panel2>
            )
        );
    }

    return (
        <DragDropWidget
            id={id}
            width={width}
            type={type}
            onDropItem={onDropItem}
        >
            {renderHit(hit)}
        </DragDropWidget>
    );
};
