import { Panel, Panel2, SubHeading, Paragraph3, Tag3 } from "@dash";
import MDEditor from "@uiw/react-md-editor";

export const CustomHitFindIndex = ({ hit }) => {
    return (
        hit !== undefined && (
            <Panel2 className={"rounded p-2 h-fit space-y-4 p-6 shadow"}>
                <SubHeading title={hit.title || ""} padding={false} />
                <Paragraph3 text={`by ${hit.author || ""}`} padding={false} />
                <div className="flex flex-row space-x-1 whitespace-break-all">
                    {"tags" in hit &&
                        hit.tags.map((t, index) => {
                            return (
                                <Tag3
                                    key={`${index}`}
                                    text={t}
                                    className={"text-xs"}
                                    textSize={"xs"}
                                />
                            );
                        })}
                </div>
                <Panel>
                    {"body" in hit && (
                        <MDEditor.Markdown
                            source={hit.body.substring(0, 200)}
                        />
                    )}
                </Panel>
                {hit && Object.keys(hit).map((h) => <Tag3 text={h} />)}
            </Panel2>
        )
    );
};
