/**
 * AlgoliaJCrewWorkspace
 *
 * @author John P. Giatropoulos <john.giatropoulos@gmail.com>
 *
 * The Workspace is comprised of the Layout, and Widgets that are configured by the User.
 * There may be multiple Workspaces of "Pages" that the user can create by dragging widgets into the Layout,
 * also defined by the user.
 *
 * @summary
 * - Children MUST comprise of an Algolia <Index> component specifying the indexName to be searched.
 * - Workspace props may be passed in the WorkspaceContext and accessed by the children in the workspace
 *
 * @param {string} workspaceName the name of the workspace, will be used to fetch the layout from config
 * @param {object} widgetComponents an object containing the keys and Components that will be used in the layout
 * @param {object} children optional, child components to be rendered instead of Layout component
 */
import React, { useEffect, useState, useContext } from "react";
import { InstantSearch, Configure, Index } from "react-instantsearch-hooks-web";
import { SearchContext } from "../contexts/SearchContext";
import {
    Workspace,
    WorkspaceContext,
    DashboardContext,
    LayoutContainer,
    Widget,
} from "@dash";
import algoliasearch from "algoliasearch";
import { CustomHits, CustomSearchbar } from "../widgets";

export const AlgoliaJCrewWorkspace = ({
    indexName = "transform_jcrew_4",
    appId = null,
    apiKey = null,
    preview,
    workspaceName,
    widgetComponents = {},
    children = null,
    scrollable = false,
    width,
    direction,
    height = "h-full",
    hitsPerPage = 40,
    page = 1,
    ...props
}) => {
    const { listeners, uuid } = props;
    const { widgetApi } = useContext(DashboardContext);
    const [searchClient, setSearchClient] = useState(null);

    useEffect(() => {
        if (searchClient === null) {
            // set the search client
            if (indexName && apiKey && appId) {
                console.log("SETTING SEARCH CLIENT");
                const searchClientTemp = algoliasearch(appId, apiKey);
                setSearchClient(searchClientTemp);
            }
        }
    });

    const handlers = {
        pageChanged: pageChanged,
    };

    useEffect(() => {
        if (widgetApi !== undefined && listeners) {
            widgetApi.registerListeners(listeners, handlers, uuid);
        }
    }, [widgetApi, listeners]);

    const [currentPage, setCurrentPage] = useState(0);

    function pageChanged(page) {
        console.log("page changed in Algolia Workspace ", page);
        setCurrentPage(page.page);
    }

    function getSearchContextValue() {
        console.log("getting search context value ", searchClient);
        return {
            searchClient,
        };
    }

    return indexName ? (
        <Workspace
            {...props}
            space={true}
            scrollable={false}
            grow={true}
            className={"space-y-4"}
        >
            <WorkspaceContext.Provider
                value={{
                    workspaceData: {
                        appId,
                        apiKey,
                        workspaceName,
                        hitsPerPage,
                        indexName,
                        searchClient,
                        page: currentPage,
                    },
                }}
            >
                {indexName && searchClient && (
                    <SearchContext.Provider value={getSearchContextValue()}>
                        <InstantSearch
                            indexName={indexName}
                            searchClient={searchClient}
                            insights={true}
                        >
                            <Configure
                                getRankingInfo={true}
                                facets={["*"]}
                                clickAnalytics
                                page={currentPage}
                            />
                            <LayoutContainer direction="row" scrollable={false}>
                                <div>
                                    <Index indexName={indexName}>
                                        {/* <CustomSearchbar
                                            indexName={indexName}
                                        /> */}
                                        {/* <div>test</div> */}
                                        {/* <CustomHits hitComponent="CustomHitJCrew" cols={3} /> */}
                                    </Index>
                                </div>
                            </LayoutContainer>
                        </InstantSearch>
                    </SearchContext.Provider>
                )}
            </WorkspaceContext.Provider>
        </Workspace>
    ) : (
        <div>Error, no index name</div>
    );
};
