/**
 * SaveDataWorkspace
 *
 * A Workspace holds the functionality (api's, libraries, etc) that can be injected
 * or accessed through Contexts by your Widgets.
 *
 * For example, if I were making an Algolia Search Workspace, I could setup my searchClient and keys
 * in this Workspace and make them available in a AlgoliaSearchContext.
 *
 * You can have a Workspace that is self-contained (no children), or if the Workspace can have children
 * the end user will be able to add similar workspace widgets into the Dash layout.
 *
 * @pacakge SaveData
 */
import { Workspace } from "@dash";
import { SaveDataContext } from "../contexts";

/**
 * Note:
 * You may specify a value for the SaveDataContext either in the Workspace or in the SaveDataContext itself.
 * You may also employ a Wrapper around the SaveDataContext to use get/set methods to update this value if desired.
 *
 * In this case we would like to pass a UNIVERSAL filename that can be used by all of the widgets in the space.
 */

export const SaveDataWorkspace = ({ filename = null, children }) => {
    console.log("filename in workspace", filename);
    return (
        <Workspace space={true} scrollable={false}>
            <SaveDataContext.Provider value={{ filename: filename }}>
                {children}
            </SaveDataContext.Provider>
        </Workspace>
    );
};
