/**
 * ListenWidget Widget
 *
 * I'm a Widget.
 * I can be placed into a Workspace that allows children (canHaveChildren = true)
 * I can also access the context for the workspace as seen in the example below
 * - Context is a great place to pass in libraries, apis, data, etc that need to be accessible in the Widget.
 * - my props are very interesting! If you configure userConfig, the keys will be the prop names and you can access them.
 *
 * @pacakge ListenWidget
 */
import React, { useState, useEffect, useContext } from "react";
import { Widget, Tag3, DashPanel3, DashboardContext } from "@dash";

export const ListenWidgetWidget = ({ id, ...props }) => {
    const { listeners, uuid } = props;
    const { widgetApi } = useContext(DashboardContext);
    const [noise, setNoise] = useState(null);

    const handlers = {
        heardSomething: heardSomething,
    };

    useEffect(() => {
        if (widgetApi !== undefined && listeners) {
            widgetApi.registerListeners(listeners, handlers, uuid);
        }
    }, [widgetApi, listeners]);

    function heardSomething(data) {
        try {
            setNoise(() => data);
        } catch (e) {
            console.log("HEARD SOMETHING ERROR", e.message);
        }
    }

    function renderNoise() {
        return (
            noise &&
            Object.keys(noise).map((sound, index) => {
                return (
                    <div
                        key={`sound-${index}`}
                        className="flex flex-row w-full rounded space-x-2 space-y-1"
                    >
                        <Tag3 text={sound} textSize="text-xs" />
                        <span className="text-xs">{noise[sound]}</span>
                    </div>
                );
            })
        );
    }

    return (
        <Widget grow={false} {...props} key={`widget-${uuid}`}>
            <DashPanel3>
                <DashPanel3.Header title="Listen" />
                <DashPanel3.Body>{renderNoise()}</DashPanel3.Body>
            </DashPanel3>
        </Widget>
    );
};
