/**
 * ReadData Widget
 *
 * I'm a Widget.
 * I can be placed into a Workspace that allows children (canHaveChildren = true)
 * I can also access the context for the workspace as seen in the example below
 * - Context is a great place to pass in libraries, widgetApis, data, etc that need to be accessible in the Widget.
 * - my props are very interesting! If you configure userConfig, the keys will be the prop names and you can access them.
 *
 * @pacakge ReadData
 */
import { useContext, useEffect, useState } from "react";
import { Widget, Paragraph2, DashboardContext, DashPanel3 } from "@dash";
import { SaveDataContext } from "../contexts";

export const ReadDataWidget = (props) => {
    const { listeners, uuid } = props;
    const { widgetApi } = useContext(DashboardContext);
    const { filename } = useContext(SaveDataContext);

    // handler map to pas to the registration of listeners
    const handlers = {
        handleSaveDataComplete: handleSaveDataComplete,
    };

    // register our listeners (from our config, and injected into this widget)
    useEffect(() => {
        if (listeners !== null && handlers && widgetApi !== null) {
            widgetApi.registerListeners(listeners, handlers, uuid);
            readData(filename);
        }
    }, [listeners, props]);

    const [notification, setNotification] = useState(null);

    /**
     * handleSaveDataComplete
     *
     * On complete of the SaveDataWidget save process
     * We would like to listen for the complete event
     * and grab the filename....and read it!
     *
     * @param {object} data
     */
    function handleSaveDataComplete(data) {
        if (data) {
            console.log("save data complete ", data);
            readData(filename);
        }
    }

    /**
     * Read data from a file
     */
    function readData(filename) {
        try {
            if (filename && widgetApi) {
                console.log("reading data");
                // make the API call here to save the data.
                widgetApi.readData({
                    filename,
                    callbackComplete: handleReadDataComplete,
                    callbackError: handleReadDataError,
                    uuid,
                });
            }
        } catch (e) {
            console.log("error reading data ", e.message);
        }
    }

    function handleReadDataComplete(e, data) {
        console.log("read data complete ", JSON.stringify(data.data));
        setNotification(data["data"]);
    }

    function handleReadDataError(error) {
        console.log("read data error ", JSON.stringify(error));
        setNotification(error);
    }

    return (
        <Widget {...props}>
            <DashPanel3>
                <DashPanel3.Header title="Read" />
                <DashPanel3.Body>
                    {filename === null && (
                        <div className={`rounded p-2 h-full bg-red-700`}>
                            <Paragraph2
                                text={
                                    "Please edit the layout, and add a filename to the SaveDataWorkspace"
                                }
                            />
                        </div>
                    )}
                    {notification &&
                        notification !== null &&
                        filename !== null &&
                        Object.keys(notification).map((d) => {
                            return (
                                // <MenuItem2 padding={false}>
                                <Paragraph2 key={d} text={notification[d]} />
                                // </MenuItem2>
                            );
                        })}
                </DashPanel3.Body>
            </DashPanel3>
        </Widget>
    );
};
