import React, { useContext } from "react";
import { ButtonIcon, Widget, DashboardContext } from "@dash";

export const CustomNSFilterBar = ({ id, ...props }) => {
    // Widget Api injected from Widget Component
    const { widgetApi } = useContext(DashboardContext);

    function handleFilterChange(e) {
        if (widgetApi) {
            console.log(
                "clicked filter ",
                e,
                `CustomNSFilterBar[${id}].filtersChanged`
            );
            // submit the event
            widgetApi.publishEvent(`CustomNSFilterBar[${id}].filtersChanged`, {
                filter: e,
            });
        }
    }

    return (
        <Widget {...props}>
            <ButtonIcon
                text="Keyword"
                onClick={() => handleFilterChange("Keyword")}
                icon="pencil"
            />
            <ButtonIcon
                text="Neural + Keyword"
                onClick={() => handleFilterChange("Keyword_Neural")}
                icon="pencil"
            />
            <ButtonIcon
                text="Neural"
                onClick={() => handleFilterChange("Neural")}
                icon="pencil"
            />
        </Widget>
    );
};
