/**
 * SearchMonitorWidget
 */
import React, { useEffect, useState, useContext } from "react";
import { Widget, DashboardContext, DashPanel3, Tag3 } from "@dash";

export const SearchMonitorWidget = (props) => {
    // get the listeners and WidgetApi from props
    const { listeners, uuid } = props;
    const { widgetApi } = useContext(DashboardContext);
    const [searchQueries, setSearchQueries] = useState([]);

    const [, updateState] = React.useState();
    const forceUpdate = React.useCallback(() => updateState({}), []);

    const handlers = {
        handleSearchChange: handleSearchChange,
    };

    useEffect(() => {
        try {
            if (listeners && widgetApi) {
                widgetApi.registerListeners(listeners, handlers, uuid);
            }
        } catch (e) {
            console.log(e.message);
        }
    });

    function handleSearchChange(message) {
        let searchQueriesTemp = searchQueries;
        const date = new Date();
        const formattedDate = date.toLocaleString("en-US", {
            hour: "numeric",
            minute: "2-digit",
            second: "2-digit",
        });

        searchQueriesTemp.push({
            time: formattedDate,
            message: message.query,
        });

        setSearchQueries(searchQueriesTemp);
        forceUpdate();
    }

    function renderSearchQueries() {
        return searchQueries.map((searchQuery, index) => {
            return (
                <li
                    className="flex flex-row text-xs space-x-2"
                    key={`sq-${index}`}
                >
                    <Tag3 text={`${searchQuery.time}`} textSize={"text-xs"} />
                    <span className="text-sm">{searchQuery.message}</span>
                </li>
            );
        });
    }

    return (
        <Widget
            className={`rounded`}
            height={"h-64 max-h-64"}
            scrollable={false}
            {...props}
        >
            <DashPanel3>
                <DashPanel3.Header title="Monitor" />
                <DashPanel3.Body scrollable={false}>
                    {/* < className="flex flex-col h-full overflow-y-scroll"> */}
                    <ul className="flex flex-col space-y-1 h-full justify-end">
                        {renderSearchQueries()}
                    </ul>
                    {/* </div> */}
                </DashPanel3.Body>
            </DashPanel3>
        </Widget>
    );
};
