/**
 * AlgoliaSearch/CustomPagination
 *
 * @author John P. Giatropoulos <john.giatropoulos@gmail.com>
 * @summary
 * CustomPagination uses hooks in order to refine the pagination of search (the page!)
 *
 *
 */
import React, { useContext } from "react";
import { Panel, Tag3, Widget, DashboardContext } from "@dash";
import { usePagination } from "react-instantsearch-hooks-web";

export const CustomPagination = ({ id, ...props }) => {
    const { widgetApi } = useContext(DashboardContext);
    const { refine, pages } = usePagination(props);

    function pageChange(page) {
        widgetApi.publishEvent(`CustomPagination[${id}].pageChanged`, {
            page: parseInt(page),
        });
    }
    return (
        <Widget {...props} direction="row">
            <Panel
                direction="row"
                width={"w-full"}
                padding={true}
                grow={true}
                className={"bg-red-500 justify-center space-x-1"}
            >
                {pages.map((page) => (
                    <Tag3
                        text={page + 1}
                        onClick={(event) => {
                            event.preventDefault();
                            console.log("refine to page ", page);
                            pageChange(page);
                            refine(page);
                        }}
                    />
                ))}
            </Panel>
        </Widget>
    );
};
