/**
 * CustomSearchbar
 *
 * A simple searchbar to handle search queries
 */
import React, { useContext, useEffect, useState } from "react";
import {
    Widget,
    AlgoliaSearchBox,
    DashboardContext,
    SelectMenu,
    LayoutContainer,
} from "@dash";
import { SearchContext } from "../contexts/SearchContext";

export const CustomSearchbar = ({
    id = "CustomSearchBar",
    indexName = "dev_find_accelerator", // if NULL show the select menu
    ...props
}) => {
    // Widget Api injected from Widget Component
    const { widgetApi } = useContext(DashboardContext);
    const { searchClient } = useContext(SearchContext);

    const [indices, setIndices] = useState([]);
    const [indexSelected, setIndexSelected] = useState(indexName);

    // useEffect(() => {
    //     if (indices.length < 1 && indexSelected !== "") {
    //         searchClient &&
    //             searchClient
    //                 .listIndices()
    //                 .then((result) => {
    //                     console.log("got indices ", result);
    //                     setIndices(result.items);
    //                         setIndexSelected(result.items[0]);
    //                 })
    //                 .catch((error) => {
    //                     console.log(error);
    //                 });
    //     }
    // }, [searchClient]);

    function handleQueryChange(query) {
        if (widgetApi) {
            // submit the event
            widgetApi.publishEvent(
                `CustomSearchbar[${id}].searchQueryChanged`,
                {
                    query,
                    indexName,
                }
            );
        }
    }

    function handleIndexChange(indexName) {
        if (indexName !== "") {
            setIndexSelected(indexName);
            if (widgetApi) {
                // submit the event
                widgetApi.publishEvent(
                    `CustomSearchbar[${id}].indexNameChanged`,
                    {
                        indexName,
                    }
                );
            }
        }
    }

    return (
        <Widget id={id} {...props} space={true} scrollable={false}>
            <LayoutContainer direction="row" space={true}>
                {indexName === "" && (
                    <SelectMenu
                        name="indexName"
                        onChange={(e) => handleIndexChange(e.target.value)}
                    >
                        {indices.length > 0 &&
                            indices.map((i) => {
                                return <option value={i.name}>{i.name}</option>;
                            })}
                    </SelectMenu>
                )}
                {indexSelected !== "" && (
                    <AlgoliaSearchBox
                        indexName={indexSelected}
                        onQueryChange={handleQueryChange}
                        className={"py-2"}
                    />
                )}
            </LayoutContainer>
        </Widget>
    );
};
