import React from "react";
import { Paragraph, Tag, Panel2 } from "@dash";

export const CustomHit = ({ hit }) => {
    const elements = Object.keys(hit).filter(v => v.indexOf("_") < 0).map(k => {
        return (
            <div className="flex flex-row space-x-4 bg-red-600 h-full">
                <Tag text={k} />
                <Paragraph>{JSON.stringify(hit[k])}</Paragraph>
            </div>
        )
        // return (<Paragraph><Tag text={k} /> {JSON.stringify(hit[k])}</Paragraph>);
    });
    return <Panel2 scrollable={false} height="h-full">{elements}</Panel2>;
};
