import React, { useContext, useEffect } from "react";
import { Widget, Panel, DashboardContext } from "@dash";
import { CustomHit } from "./Custom/CustomHit";
import { HitsGrid } from "./HitsGrid";
import { CustomHitFindIndex } from "./Custom/CustomHitFindIndex";
import { CustomHitRL } from "./Custom/CustomHitRL";
import { CustomHitJCrew } from "./Custom/CustomHitJCrew";

// here is a map of all of the Custom Components that I will expose
// to the user in the CustomHits.dash.js configuration
// The user may choose which Custom Hit Component they wish.
const hitComponentsMap = {
    CustomHit: CustomHit,
    CustomHitFindIndex: CustomHitFindIndex,
    CustomHitRL: CustomHitRL,
    CustomHitJCrew: CustomHitJCrew,
};

export const CustomHits = ({
    indexName = null,
    hitComponent = "CustomHit",
    cols = 1,
    neuralFilter = "",
    ...props
}) => {
    const { id } = props;
    const { widgetApi } = useContext(DashboardContext);

    function handleDropItem(item) {
        console.log("Dropped item", item);
        widgetApi.publishEvent(`CustomHits[${id}].onDropHit`, item);
    }

    return (
        <Widget id={id} {...props} space={true} scrollable={false}>
            <HitsGrid
                hitComponent={hitComponentsMap[hitComponent]}
                cols={cols}
                onDropItem={handleDropItem}
                neuralFilter={neuralFilter}
                {...props}
            />
        </Widget>
    );
};
