import { Panel2, Paragraph, Tag2, Tag3, Paragraph3, Tag } from "@dash";
import { DragDropWidget } from "./DragDropWidget";
import { isNeural, isNeuralAndKeyword } from "../../utils";

export const CustomHitJCrew = ({
    id,
    width,
    type = "hit-widget",
    hit,
    queryID = null,
    onDropItem = null,
}) => {
    function renderHit(hit) {
        // determine if the hit is a neural, neural + keyword hit
        const isNeuralHit = isNeural(hit);
        const isNeuralAndKeywordHit = isNeuralAndKeyword(hit);

        const backgroundColorStyle = () => {
            var c = null;
            if (isNeuralAndKeywordHit === true) {
                c = "bg-purple-700";
            }
            if (isNeuralHit && isNeuralAndKeywordHit === false) {
                c = "bg-blue-600";
            }
            return c;
        };

        return (
            hit !== undefined && (
                <Panel2
                    padding={false}
                    space={true}
                    backgroundColor={backgroundColorStyle()}
                >
                    <Panel2.Header padding={false}>
                        <div className="flex flex-col bg-gray-400 rounded h-full justify-center items-center p-0 bg-white overflow-clip">
                            <img
                                src={hit.image_link}
                                className="p-0 object-contain"
                            />
                        </div>
                    </Panel2.Header>
                    <Panel2.Body backgroundColor={backgroundColorStyle}>
                        <Paragraph text={hit.title} padding={false} />
                        <Paragraph3
                            text={hit.description.substring(0, 100)}
                            padding={false}
                            className={"text-gray-400"}
                        />
                    </Panel2.Body>
                    {/* </div> */}
                    <Panel2.Footer>
                        <div className="flex flex-col space-y-1">
                            {/* <div className="flex flex-row space-x-1"> */}
                            <Tag2
                                text={`$${hit.price}`}
                                backgroundColor={"bg-green-700 text-xs"}
                            />
                            {hit["Brand"] !== undefined &&
                                hit["Brand"] !== "" &&
                                hit["Brand"] !== null && (
                                    <Tag3
                                        text={`${hit["Brand"]}`}
                                        backgroundColor={"bg-orange-800"}
                                    />
                                )}
                            {/* </div> */}
                            <div className="flex flex-wrap space-x-1 space-y-1">
                                {hit["Gender"] !== undefined &&
                                    hit["Gender"] !== "" && (
                                        <Tag3
                                            text={`${hit["Gender"]}`}
                                            backgroundColor={"bg-orange-800"}
                                        />
                                    )}
                                {hit["Condition"] !== undefined &&
                                    hit["Condition"] !== "" && (
                                        <Tag3
                                            text={`${hit["Condition"]}`}
                                            backgroundColor={"bg-orange-800"}
                                        />
                                    )}
                                {hit["pattern"] !== undefined &&
                                    hit["pattern"] !== "" && (
                                        <Tag3
                                            text={`${hit["pattern"]}`}
                                            backgroundColor={"bg-orange-800"}
                                        />
                                    )}
                                {hit["material"] !== undefined &&
                                    hit["material"] !== "" && (
                                        <Tag3
                                            text={`${hit["material"]}`}
                                            backgroundColor={"bg-orange-800"}
                                        />
                                    )}
                                {hit["g:color"] !== undefined &&
                                    hit["g:color"] !== "" && (
                                        <Tag3
                                            text={`${hit["g:color"]}`}
                                            backgroundColor={"bg-orange-800"}
                                        />
                                    )}
                                {hit["inStock"] !== undefined && (
                                    <Tag3
                                        text={`In Stock ${hit["inStock"]}`}
                                        backgroundColor={"bg-orange-800"}
                                    />
                                )}
                            </div>
                        </div>
                        {/* </div> */}
                    </Panel2.Footer>
                    <Panel2.Footer className={"bg-blue-900"}>
                        <div className="flex flex-row space-x-1">
                            {isNeuralHit === false &&
                                isNeuralAndKeywordHit === false && (
                                    <Tag3 text={`Keyword match only`} />
                                )}
                            {isNeuralHit === true && (
                                <Tag3
                                    text={`NS ${Math.round(
                                        hit._rankingInfo["semanticScore"] * 100,
                                        2
                                    )}%`}
                                />
                            )}
                            {isNeuralAndKeywordHit === true && (
                                <Tag3
                                    text={`KW ${Math.round(
                                        hit._rankingInfo["keywordScore"] * 100,
                                        2
                                    )}%`}
                                />
                            )}
                        </div>
                    </Panel2.Footer>
                    {hit.promoted === true && (
                        <Panel2.Footer>
                            <Tag
                                text={`QID:${queryID}`}
                                className={"font-normal text-xs text-gray-500"}
                            />
                        </Panel2.Footer>
                    )}
                    <Panel2.Footer>
                        <Tag
                            text={`QID:${queryID}`}
                            className={"font-normal text-xs text-gray-500"}
                        />
                    </Panel2.Footer>
                    {/* </div> */}
                </Panel2>
            )
        );
    }

    return (
        <DragDropWidget
            id={id}
            width={width}
            type={type}
            onDropItem={onDropItem}
        >
            {renderHit(hit)}
        </DragDropWidget>
    );
};
