/**
 * SampleProvider Widget
 *
 * A test widget that demonstrates provider requirements and automatic
 * provider setup flow.
 *
 * @package SampleProvider
 */

import { Widget, Heading2, SubHeading3, Panel } from "@dash";

const defaultRequiredProviders = [
    {
        type: "algolia",
        credentialSchema: {
            appId: {
                type: "text",
                displayName: "Application ID",
                instructions: "Your Algolia App ID",
                required: true,
                secret: true,
            },
            apiKey: {
                type: "text",
                displayName: "API Key",
                instructions: "Read-only API key",
                required: true,
                secret: true,
            },
            indexName: {
                type: "text",
                displayName: "Index Name",
                instructions: "Default index to search",
                required: true,
                secret: false,
            },
        },
    },
];

const resolveRequiredProviders = (requiredProviders, widgetConfig) => {
    if (Array.isArray(requiredProviders) && requiredProviders.length > 0) {
        return requiredProviders;
    }

    if (
        widgetConfig &&
        Array.isArray(widgetConfig.requiresProviders) &&
        widgetConfig.requiresProviders.length > 0
    ) {
        return widgetConfig.requiresProviders;
    }

    return defaultRequiredProviders;
};

export const SampleProviderWidget = ({
    title = "Provider Setup Test",
    subtitle = "This widget requires a provider to render",
    requiredProviders,
    widgetConfig,
    onProviderSelect,
    ...props
}) => {
    const resolvedProviders = resolveRequiredProviders(
        requiredProviders,
        widgetConfig
    );

    return (
        <Widget
            {...props}
            width={"w-full"}
            height={"h-full"}
            requiredProviders={resolvedProviders}
            onProviderSelect={onProviderSelect}
        >
            <Panel className={"p-6"}>
                <Heading2 title={title} />
                <SubHeading3 title={subtitle} />
                <div className="text-sm text-gray-600 mt-4">
                    This widget demonstrates the automatic provider detection
                    flow. If the required provider is not registered, the
                    Missing Provider Prompt will appear.
                </div>
            </Panel>
        </Widget>
    );
};
