/**
 * SampleError Widget
 *
 * This widget intentionally throws an error to demonstrate the error boundary.
 * 
 * @package SampleError
 */

import { Widget, Heading2, SubHeading3, Panel } from "@dash";

const ErrorComponent = () => {
    // Throw error inside the component tree, after Widget has wrapped it
    throw new Error("Intentional error to test Widget Error Boundary!");
    return null;
};

export const SampleErrorWidget = ({
    title = "This will error",
    subtitle = "Error boundary test",
    ...props
}) => {
    return (
        <Widget {...props} width={"w-full"} height={"h-full"}>
            <Panel className={"p-6"}>
                <Heading2 title={title} />
                <SubHeading3 title={subtitle} />
                <ErrorComponent />
            </Panel>
        </Widget>
    );
};
