/**
 * Algolia Utility methods
 */
const isNeural = (hit) =>
    hit._rankingInfo.hasOwnProperty("semanticScore") === true;

const isNeuralAndKeyword = (hit) =>
    hit._rankingInfo.hasOwnProperty("semanticScore") === true &&
    hit._rankingInfo.hasOwnProperty("keywordScore") === true &&
    hit._rankingInfo["keywordScore"] > 0;

export { isNeural, isNeuralAndKeyword };
