import { useEffect, useContext } from "react";
import {
    Widget,
    AlgoliaRefinementList,
    Panel,
    DashPanel,
    DashboardContext,
    Tag,
} from "@dash";
import { useRefinementList } from "react-instantsearch-hooks-web";

/**
 * CustomRefinements
 */

export const CustomRefinements = ({
    id,
    attribute = null,
    attributeDisplay = null,
    ...props
}) => {
    const { widgetApi } = useContext(DashboardContext);

    
    const { items, refine } = useRefinementList({
        attribute,
        ...props,
    });
    

    // const items = [];
    // const refine = () => {}

    function handleRefinementChange(data) {
        if (widgetApi) {
            // submit the event
            widgetApi.publishEvent(
                `CustomRefinements[${id}].refinementsChanged`,
                {
                    attribute,
                    value: data,
                }
            );
        }
    }

    return (
        attribute &&
        attributeDisplay && (
            <Widget {...props}>
                <DashPanel>
                    {attributeDisplay &&
                        typeof attributeDisplay === "string" && (
                            <DashPanel.Header title={attributeDisplay} />
                        )}
                    {attribute !== undefined &&
                        attribute !== null &&
                        attribute !== "" &&
                        typeof attribute === "string" && (
                            <DashPanel.Body>
                                <ul className={`flex flex-col space-y-1`}>
                                    {items.map((item) => (
                                        <li
                                            key={item.label}
                                            className="px-2 py-1 cursor-pointer hover:text-indigo-600 hover:bg-gray-800 rounded justify-between flex flex-row xl:flex-row w-full"
                                            onClick={(event) => {
                                                console.log(
                                                    "refining item ",
                                                    item.value
                                                );
                                                event.preventDefault();
                                                refine(item.value);
                                                handleRefinementChange(
                                                    item.value
                                                );
                                            }}
                                        >
                                            <span
                                                className={`text-sm text-gray-300 hover:text-indigo-500 ${
                                                    item.isRefined &&
                                                    "font-bold text-green-500"
                                                }`}
                                            >
                                                {item.label}
                                            </span>
                                            <Tag
                                                text={item.count}
                                                color="bg-gray-700"
                                                textSize="text-xs"
                                            />
                                        </li>
                                    ))}
                                </ul>
                            </DashPanel.Body>
                        )}
                </DashPanel>
            </Widget>
        )
    );
};
