import React, { useRef } from "react";
/**
 * HitsGrid
 *
 * A simple Widget to display Algolia hits in a grid.
 */
import { useInfiniteHits } from "react-instantsearch-hooks-web";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import { isNeural, isNeuralAndKeyword } from "../utils";

export const HitsGrid = ({
    hitComponent,
    cols = 1,
    onDropItem,
    neuralFilter = "", // Keyword_Neural, Keyword, Neural
    ...props
}) => {
    const { hits } = useInfiniteHits(props);
    const sentinelRef = useRef(null);

    function handleDropItem(item) {
        onDropItem(item);
    }

    // based on the user and the size of the viewport,
    // we would like to make sure the grid is responsive.
    function getCols() {
        return `grid-cols-${
            cols > 1 ? cols - 1 : cols
        } xl:grid-cols-${cols} gap-3`;
    }

    /**
     * filterNeuralResults
     * If applied, will filter out the hits that are either NS, Keyword, or Both
     * @param {*} hits
     * @returns
     */
    function filterNeuralResults(hits) {
        return hits.filter((hit) => {
            // neuralFilter;

            let filteredHit = hit;

            switch (neuralFilter) {
                case "Keyword":
                    filteredHit =
                        isNeural(hit) === false &&
                        isNeuralAndKeyword(hit) === false
                            ? hit
                            : null;
                    break;

                case "Keyword_Neural":
                    filteredHit = isNeuralAndKeyword(hit) === true ? hit : null;
                    break;

                case "Neural":
                    filteredHit =
                        isNeural(hit) === true &&
                        isNeuralAndKeyword(hit) === false
                            ? hit
                            : null;
                    break;

                default:
                    return filteredHit;
            }
            return filteredHit;
        });
    }

    return (
        hitComponent !== null &&
        hitComponent !== undefined && (
            <DndProvider backend={HTML5Backend}>
                <div className={`grid ${getCols()}`}>
                    {filterNeuralResults(hits).map((hit, index) => {
                        const HitComponent = hitComponent;
                        return (
                            <HitComponent
                                id={hit.objectID}
                                key={`grid-${index}`}
                                hit={hit}
                                onDropItem={handleDropItem}
                                queryID={hit.__queryID}
                            />
                        );
                    })}
                    <div ref={sentinelRef} />
                </div>
            </DndProvider>
        )
    );
};
